.featured{
  height: 100vh;
}

.featured--vertical{
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111, transparent);
}

.featured--horizontal{
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111, transparent);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
}

.featured--name{
  font-size: 60px;
  font-weight: bold;
}

.featured--info{
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.featured--points{
  display: inline-block;
  color: #46d369;
  margin-right: 15px;
}

.featured--year{
  display: inline-block;
  margin-right: 15px;
}

.featured--seasons{
  display: inline-block;
}

.featured--description{
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
}

.featured--buttons{
  margin-top: 15px;
}

.featured--watchButton, .featured--myListButton{
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  cursor: pointer;
  opacity: 1;
}

.featured--watchButton{
  background-color: #fff;
  color: #000;
}

.featured--myListButton{
  background-color: #333;
  color: #fff;
}

.featured--watchButton:hover, .featured--myListButton:hover{
  opacity: 0.7;
  transition: all ease 0.2;
}

.featured--genres{
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}

@media(max-width: 760px){
  .featured{
    height: 85vh;
  }

  .featured--name{
    font-size: 40px;
  }

  .featured--info{
    font-size: 16px;
  }

  .featured--description{
    max-width: 100%;
    font-size: 14px;
    margin-right: 30px;
  }

  .featured--watchButton, .featured--myListButton{
    font-size: 16px;
  }

  .featured--genres{
    font-size: 14px;
  }
}