header{
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  padding: 20px 30px;
  background-color: transparent;
}

header.black{
  background-color: #141414;
  transition: all ease 0.5s;
}

.header--logo, .header--user{
  height: 25px;
}

.header--user{
  height: 35px;
}

.header--user img{
  border-radius: 3px;
}

.header--logo img, .header--user img{
  height: 100%;
}